.demo-panel {
  padding: 15px 15px 10px 15px;
  position: fixed;
  left: -246px;
  top: 200px;
  z-index: 5;
  text-align: left;
  font-size: 18px;
  font-weight: 400;
  color: #333;
  text-shadow: none;
  background: #fff;
  border: 1px solid #888;
  border-left: 0;
  transition: 0.25s left; }

.demo-panel.active {
  left: 0; }

.demo-panel a {
  color: #08c;
  transition: 0.12s color; }

.demo-panel a:hover {
  color: #D44032; }

.demo-panel-activator {
  width: 36px;
  height: 36px;
  margin: 0;
  padding: 0;
  position: absolute;
  left: 100%;
  top: -1px;
  font-size: 22px;
  line-height: 36px;
  color: #333;
  text-decoration: none;
  background: #fff;
  border: 1px solid #888;
  border-left: 0;
  cursor: pointer;
  outline: none;
  transition: 0.12s color; }

.demo-panel-activator:hover {
  color: #08c; }

.demo-panel-activator i {
  line-height: 36px; }

.demo-panel-heading {
  margin: 15px 0 0 0;
  font-size: 16px; }

.demo-panel-themes {
  margin: 0 0 0 -5px;
  padding: 0;
  list-style: none;
  text-align: center; }

.demo-panel-themes li {
  float: left;
  border: 5px solid transparent;
  transition: 0.15s border-color; }

.demo-panel-themes li:nth-child(2n + 1) {
  clear: left; }

.demo-panel-themes li:hover {
  border-color: #00B159; }

.demo-panel-themes li.active {
