.demo-panel-themes img {
  cursor: pointer;
  vertical-align: top; }

/*
        Page
==================================================================================================*/
html {
  display: table;
  width: 100%;
  height: 100%; }

body {
  display: table-cell;
  margin: 0;
  vertical-align: middle;
  text-align: center;
  font: 300 20px/1.4 Roboto, "Open Sans", Helvetica, Arial, sans-serif;
  color: #fff;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.66);
  background: #4D4646 50% 50% no-repeat;
  background-size: cover; }

.theme-fire {
  color: #fff !important; }

/*
        Background
==================================================================================================*/
.background-container {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0; }

/* Hide background in old Opera because of lags */
noindex:-o-prefocus, .background-container {
  display: none; }

.background-vignette,
.background-noise {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  pointer-events: none; }

.background-vignette {
  background-image: radial-gradient(transparent 30%, rgba(0, 0, 0, 0.8) 100%);
  /* IE9 workaround */
  box-shadow: inset 0 0 600px #000 \9; }

.background-noise {
  background: url(data:image/png;base64,);
  opacity: 0.85; }

/*
        Links
==================================================================================================*/
a {
  color: #32a7d3; }

a:hover,
a:focus {
  color: #2588ad; }

/*
        Layout
==================================================================================================*/
.content {
  padding: 40px 0;
  position: relative; }

/*
        Text content
==================================================================================================*/
.header {
  margin-bottom: 30px;
  font-size: 80px;
  line-height: 1.2;
  font-weight: 100; }

.description {
  margin-bottom: 60px; }

/*
        Countdown
==================================================================================================*/
.countdown {
  padding: 30px 0 45px;
  background: rgba(0, 0, 0, 0.5); }

.countdown-label {
  margin-top: 10px;
  text-align: center;
  font-size: 24px; }

.countdown-number {
  font-size: 124px;
  line-height: 1;
  font-weight: 100;
  text-shadow: none; }

/*
        Form and links container
==================================================================================================*/
.form-and-links-container {
  padding-bottom: 25px;
  overflow: hidden; }

/*
        Subscription form
==================================================================================================*/
.subscription-form {
  margin: 0;
  padding: 0 15px 15px;
  position: relative;
  background: rgba(0, 0, 0, 0.5); }

/*      Email field
----------------------------------------------------------*/
.subscription-form-email {
  width: 450px;
  height: 50px;
  padding: 0 12px;
  margin: 0;
  box-sizing: border-box;
  vertical-align: top;
  font-size: 20px;
  line-height: 1.4;
  font-family: inherit;
  font-weight: inherit;
  color: #222;
  text-shadow: none;
  background: #fff;
  border: 0;
  border-radius: 0;
  transition: none; }

.subscription-form-email:focus {
  padding: 0 12px 0 9px;
  color: #222;
  border-width: 3px 0 3px 3px;
  border-style: solid;
  box-shadow: none;
  outline: none; }

.subscription-form-email:invalid:focus:invalid {
  box-shadow: none; }

/* IE8 hack for vertical text aligning in the email field */
@media \0screen {
  .subscription-form-email {
    padding-top: 11px; }
  .subscription-form-email:focus {
    padding-top: 8px; } }

/* Placeholder */
::-webkit-input-placeholder {
  color: #777; }

:-moz-placeholder {
  /* Firefox 18- */
  color: #777; }

::-moz-placeholder {
  /* Firefox 19+ */
  color: #777; }

:-ms-input-placeholder {
  color: #777; }

/*      Button
----------------------------------------------------------*/
.subscription-form-submit {
  height: 50px;
  padding: 0;
  box-sizing: border-box;
  vertical-align: top;
  font-size: 20px;
  font-family: inherit;
  font-weight: inherit;
  color: #fff;
  text-shadow: none;
  border: 0;
  border-radius: 0;
  box-shadow: none;
  cursor: pointer;
  transition: 0.15s; }

.subscription-form-submit:hover,
.subscription-form-submit:focus,
.subscription-form-submit:active,
.subscription-form-submit:focus:active {
  color: #fff;
  outline: none; }

.subscription-form-submit {
  width: 120px;
  margin-left: 0 !important; }

/*      Tooltip
----------------------------------------------------------*/
.subscription-form-tooltip {
  margin: -5px 0 0;
  padding: 8px 15px;
  box-sizing: border-box;
  position: absolute;
  top: 120%;
  left: 50%;
  z-index: 1;
  text-shadow: none;
  opacity: 0;
  white-space: nowrap; }

.subscription-form-tooltip:before {
  content: '';
  margin: -8px 0 0 -8px;
  position: absolute;
  left: 50%;
  top: 0;
  border: solid transparent;
  border-width: 0 8px 8px; }

.subscription-form-tooltip.error {
  color: #fff;
  background: #d44032; }

.subscription-form-tooltip.error:before {
  border-bottom-color: #d44032; }

.subscription-form-tooltip.success {
  color: #fff;
  background: #00b159; }

.subscription-form-tooltip.success:before {
  border-bottom-color: #00b159; }

/*
        Social links
==================================================================================================*/
[class^="icon-"], [class*=" icon-"] {
  display: inline-block;
  width: auto;
  height: auto;
  margin: 0;
  vertical-align: middle;
  line-height: 1;
  background: transparent; }

.social-links {
  margin-top: 50px;
  font-size: 0; }

.social-links > a {
  width: 50px;
  height: 50px;
  margin: 6px;
  font-size: 28px;
  line-height: 54px;
  color: #fff;
  text-decoration: none;
  text-shadow: none;
  background: rgba(0, 0, 0, 0.5);
  transition: 0.3s; }

.social-links > a:hover,
.social-links > a:focus {
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg); }

/*
        Responsiveness
==================================================================================================*/
@media (max-width: 1200px) {
  /* Hide background on small devices */
  .background-container {
    display: none; } }

@media (max-width: 979px) {
  .header {
    font-size: 64px; }
  .countdown {
    padding: 20px 0 25px; }
  .countdown-number {
    font-size: 96px; }
  .subscription-form-email {
    width: 306px; } }

@media (max-width: 767px) {
  body {
    padding: 20px; }
  body,
  .subscription-form-email,
  .subscription-form-submit {
    font-size: 18px; }
  .header {
    font-size: 56px; }
  .countdown-item {
    width: 50%;
    margin: 20px 0;
    float: left; }
  .countdown-number {
    font-size: 80px; }
  .countdown-label {
    margin-top: 5px; }
  .subscription-form {
    padding: 0 0 35px; }
  .subscription-form-email {
    width: 310px;
    height: 40px;
    padding: 0 8px;
    box-sizing: border-box; }
  .subscription-form-email:focus {
    padding: 0 8px 0 5px; }
  .subscription-form-submit {
    width: 100px;
    height: 40px; }
  .subscription-form-tooltip {
    margin-top: -25px;
    padding: 4px 10px;
    white-space: normal; }
  .social-links > a {
    width: 40px;
    height: 40px;
    font-size: 24px;
    line-height: 42px; } }

@media (max-width: 550px) {
  .subscription-form,
  .subscription-form .input-group,
  .subscription-form .subscription-form-email,
  .subscription-form .input-group-btn {
    display: block;
    float: none;
    box-sizing: border-box; }
  .subscription-form .input-group-btn,
  .subscription-form .subscription-form-submit {
    width: 100%; }
  .subscription-form .subscription-form-email {
    margin-bottom: 15px; }
  .subscription-form .subscription-form-email:focus {
    border-right-width: 3px; } }

